import React from 'react'

// Libraries
import { Col, Container, Row } from 'react-bootstrap'
import { Parallax } from "react-scroll-parallax";

// Components
import PortfolioJustifiedGallery from '../../Components/Portfolio/PortfolioJustifiedGallery'

// Data
import { portfolioJustifiedGalleryData } from '../../Components/Portfolio/PortfolioData';

const PortfolioJustifiedGalleryPage = (props) => {
  return (
    <div style={props.style}>
      {/* Parallax Section Start */}
      <div className="py-[80px] h-auto overflow-hidden md:relative md:py-[40px]">
        <Parallax className="lg-no-parallax absolute top-[0] w-full h-full lg:bg-cover bg-no-repeat" translateY={[-40, 40]} style={{ backgroundImage: `url(/assets/img/webp/portfolio-bg.webp)` }}></Parallax>
        <Container className="h-full relative">
          <Row className="justify-center h-[300px] sm:h-[250px]">
            <Col xl={6} lg={6} sm={8} className="text-center flex justify-center flex-col font-serif">
              <h1 className="text-gradient bg-gradient-to-r from-[#556fff] via-[#e05fc4] to-[#ff798e] mb-[15px] inline-block text-xmd leading-[20px]">Portfolio justified gallery</h1>
              <h2 className="text-darkgray font-medium -tracking-[1px] mb-0">Portfolio justified</h2>
            </Col>
          </Row>
        </Container>
      </div>
      {/* Parallax Section End */}

      <section className="pt-0 pb-[130px] lg:pb-[90px] md:pb-[75px] sm:pb-[50px] relative md:px-[3%] sm:px-0 lg:py-[90px] md:py-[75px] sm:py-[50px] overflow-hidden">
        <Container fluid>
          <Row>
            <Col xs={12} className="p-0">
              <PortfolioJustifiedGallery
                grid="grid grid-4col xl-grid-4col lg-grid-3col md-grid-2col sm-grid-2col xs-grid-1col gutter-large mt-0"
                data={portfolioJustifiedGalleryData}
              />
            </Col>
          </Row>
        </Container>
      </section>
    </div>
  )
}

export default PortfolioJustifiedGalleryPage